import * as React from 'react';
import { Timeline, TimelineEvent } from 'react-event-timeline';
import Layout from '../components/layout';

const IndexPage = () => {
  return(
    <Layout pageTitle='About'>
      <h1>About Me</h1>
      <p>Hello, I am Yihua Liu. My Chinese name is  (pinyin: li y hu; IPA: ljo i xw).</p>
      <p>I am a senior student at University of Michigan - Shanghai Jiao Tong University Joint Institute, major in Electrical and Computer Engineering. I am interested in hardware/software co-design.</p>
      <h1>Education</h1>
      <div style={{height: '100vh', fontFamily: 'Merriweather Sans'}}>
      <Timeline>
      <TimelineEvent
        title="Shanghai Jiao Tong University"
        titleStyle={{ fontSize: '15pt', fontWeight: '600' , fontFamily: 'Open Sans' }}
        subtitle="B. S. E. in Electrical and Computer Engineering"
        subtitleStyle={{ fontSize: '14pt', fontWeight: '400', fontFamily: 'Mukta'}}
        contentStyle={{
          'padding': '0em 0em',
          'boxShadow': 'none',
          'line-height': '1.8',
          'margin': '0 0',
          'backgroundColor': 'rgb(255, 255, 255)'
        }}
        style={{ fontSize: '14pt', fontWeight: '300' }}
        createdAt={"Sept 2018 - Aug 2022"}
        icon={<i />}
        iconColor="#6fba1c"
      >
      </TimelineEvent>
      <TimelineEvent
        title="The Affiliated High School of Shanxi University"
        titleStyle={{ fontSize: '14pt', fontWeight: '600' , fontFamily: 'Open Sans' }}
        subtitle="High School"
        subtitleStyle={{ fontSize: '14pt', fontWeight: '400', fontFamily: 'Mukta'}}
        contentStyle={{
          'padding': '0em 0em',
          'boxShadow': 'none',
          'line-height': '1.8',
          'margin': '0 0',
          'backgroundColor': 'rgb(255, 255, 255)'
        }}
        style={{ fontSize: '14pt', fontWeight: '300' }}
        createdAt="Sept 2012 - June 2018"
        icon={<i />}
        iconColor="#6fba1c"
      >
      </TimelineEvent>
      </Timeline>
      </div>
    </Layout>
  )
}

export default IndexPage
